@import url(https://fonts.googleapis.com/css?family=Ubuntu:R|Bebas+Neue|Mrs+Sheppards|Righteous);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:graysscale;background-color:whitesmoke;color:#323232}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

#home{font-size:5em;min-height:100vh;text-align:center;font-family:'Ubuntu', sans-serif}#title{display:flex;align-items:center;justify-content:center;height:auto;min-height:inherit}@media screen and (max-width: 1200px){#title{flex-direction:column}}#name{height:auto}#name p{font-size:11vw}@media screen and (max-width: 1200px){#name p{font-size:12rem}}@media screen and (max-width: 960px){#name p{font-size:8rem}}@media screen and (max-width: 600px){#name p{font-size:4rem}}#brush_stroke{background:url(/static/media/brush-stroke.d77f61fe.png);background-repeat:no-repeat;background-size:100% 95%;padding:1rem 7.5rem}@media screen and (max-width: 600px){#brush_stroke{padding:1rem 3rem}}#external_links{width:100%}@media screen and (max-width: 1200px){#external_links{display:none}}#self-portrait{width:30vw;margin-left:2.5rem;border-radius:2rem;box-shadow:10px 10px rgba(122,63,181,0.5);transition:box-shadow 0.3s ease}@media screen and (max-width: 1200px){#self-portrait{margin-left:0;-webkit-transform:translateY(-100px);transform:translateY(-100px);width:30rem}}@media screen and (max-width: 960px){#self-portrait{width:25rem}}@media screen and (max-width: 600px){#self-portrait{-webkit-transform:translateY(-50px);transform:translateY(-50px);width:17.5rem}}#self-portrait:hover{box-shadow:5px 5px rgba(122,63,181,0.5)}.image_links{margin:0 1rem}.icons{color:#181818;opacity:0.75;font-size:2rem;transition:opacity 0.3s ease, -webkit-transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease}.icons:hover{opacity:1;-webkit-transform:translateY(-5px);transform:translateY(-5px)}@font-face{font-family:"Unforgotton";src:url(/static/media/unforgotten.regular.0e4eebb3.ttf)}

#base{font-family:"Ubuntu", sansSerif;min-height:100vh;height:auto;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 1200px){#base{flex-direction:column}}.introduction{width:35vw;margin-left:5rem}.introduction h1{font-size:3rem;text-align:left}.introduction p{font-size:1.5rem;text-align:left}@media screen and (max-width: 1200px){.introduction{margin:0;width:75vw}}@media screen and (max-width: 850px){.introduction{width:80vw}.introduction h1{font-size:2.5rem}.introduction p{font-size:1.25rem}}@media screen and (max-width: 480px){.introduction h1{font-size:2rem}.introduction p{font-size:0.75rem}}

li{padding:0.75% 1%;display:inline-block;font-size:1.5rem}@media screen and (max-width: 1024px){li{font-size:1rem}}@media screen and (max-width: 600px){li{font-size:0.75rem}}#skills_section{font-family:"Ubuntu", sansSerif;min-height:100vh;height:auto;display:flex;justify-content:center;align-items:center}@media screen and (max-width: 1024px){#skills_section{flex-direction:column}}#skills{width:60vw;text-align:left}@media screen and (max-width: 1024px){#skills{width:85vw}}@media screen and (max-width: 600px){#skills{width:90vw}}#coding li{background-color:rgba(16,175,95,0.555);border-radius:0.15em;margin:0.5% 1%}#interests li{background-color:rgba(236,73,73,0.39);border-radius:0.15em;margin:0.5% 1%}.categories{list-style-type:none}.categories h1{font-size:2rem;margin-bottom:1rem;font-family:"Bebas Neue", sans-serif;letter-spacing:2.5px}@media screen and (max-width: 600px){.categories h1{font-size:1.5rem;letter-spacing:2px}}.languageProficiency{padding:0.25em;background-color:rgba(44,196,196,0.39);border-radius:0.15em;font-size:1em;display:inline-block}

#header{display:flex;align-items:center;position:fixed;top:0;width:100%;height:12.5vh;z-index:1;background-color:whitesmoke}@media screen and (max-width: 1200px){#header{display:none}}#returnHome{font-family:"Mrs Sheppards", sans-serif;font-size:3em;text-decoration:none;color:#323232;opacity:0.75;margin-left:5rem;transition:opacity 300ms ease}#returnHome:hover{opacity:1}#links{position:absolute;display:flex;align-items:center;right:0;top:0;margin-right:2rem;height:100%}.link{margin:0 2rem;padding:0.25em 0.5rem;font-size:1.75rem;font-family:"Bebas Neue", sans-serif;letter-spacing:2.5px;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none;color:#181818;text-decoration:none}.link::after{content:"";position:absolute;left:50%;bottom:11px;z-index:-1;width:0;height:0.3em;opacity:0;background:rgba(143,219,181,0.856);transition:all 350ms cubic-bezier(0.5, 1.6, 0.15, 0.9)}.link:hover::after{left:0;width:100%;opacity:1}

